.our-services {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 40px;
  background: #fff;
}
.our-services:focus-visible .title{
  transform: scale(3);
}
.our-services .title {
  font-family: 'Heebo';
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 59px;
  text-transform: uppercase;
  color: #000000;
  margin: 0;
}

.our-services .services {
  margin-top: 40px;
  display: grid;
  grid-template-columns: 250px 250px 250px;
  row-gap: 48px;
  column-gap: 88px;
}

.our-services .services .service {
  border: 1px solid #FFE8F4;
  border-radius: 8px;
  padding: 24px 32px;
}

.our-services .services .service img {
  height: 40px;
  width: 40px;
  margin-bottom: 20px;
}

.our-services .services .service h5 {
  font-family: 'Heebo';
  font-style: normal;
  font-weight: 500;
  font-size: 22px;
  line-height: 32px;
  color: #181423;
  margin: 0;
}